.control {
  display: flex;
  gap: 10px;
  padding-bottom: 50px;

  & > * {
    width: 80px;
    height: 80px;
    padding: 10px;
    border-radius: 50%;
    background: rgb(134, 211, 247);
    border: 2px solid rgb(35, 113, 150);
  }
}

.time {
  font-size: 24px;
}

.show {
  display: block;
}

.hide {
  display: none;
}
